*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: sans-serif;
  font-weight: 400;
  text-decoration: none;
  width: 100%;

}
body{
  min-height: 100vh;

}
.header{
  text-align: center;
  font-size: 1.3rem;
  background-color: rgb(33, 189, 189);
  padding: 20px 10px;
  text-transform: capitalize;  
}
#root{
  display: flex;
  justify-content: center;
  align-content: center;

}

.app{
  width: 100%;
  max-width: 800px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  border: 2px solid rgb(0, 0, 0);
}
/*HEADER SECTION STYLING START*/
/* 

 */

/*HEADER SECTION STYLING END*/
/*MISSING SECTION STYLING START*/

.msg,.missing{
  text-align: center;
  font-size: 2rem;
  text-transform: capitalize;
}
/*MISSING SECTION STYLING END*/

/*NAV BAR SECTION STYLING START*/

.navbar{
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  background-color: black;
  color: white;


}
ul{
  display: flex;
  list-style-type: none;
  margin: 10px 0;


}
li{
  padding: 20px;
  margin: 10px 0px;
  font-size: 1.6rem;
  border: 1px dotted red;

}
input{
  width: 300px;
  height: 50px;
  font-size: 1.3rem;
  margin-top: 10px;
  padding: 10px;


}
li:hover,
li:focus{
  background-color: white;
  color: black;
  cursor: pointer;
}


/*NAV BAR SECTION STYLING END*/


/*VIEWPOST SECTION STYLING START*/
.deletebtn{
   width: 150px;
   height: 50px;
   font-size: 1.3rem;
   color: white;
   border: none;
   border-radius: 10px;
   margin: 10px 0 ;
   background-color: red;
}


/*VIEWPOST SECTION STYLING END*/






/*FOOTER SECTION STYLING START*/

.Footer{
  padding: 3rem 0px;
  color: rgb(50, 32, 188);
  background-color: black;
  text-align: center;
  font-size: 1.4rem;
  
}
.Footer h4{
  padding: 0 5rem;
}


/*FOOTER SECTION STYLING END*/




/*POST PAGE STYLING START..*/
.postarea{
    border-bottom: 2px solid black;
    padding: 2rem;
    background-color: bisque;
    height: 100%;
    align-items: center;

}
.posttitle {
       font-size: 2.3rem;
       color: black;
       text-transform: capitalize;

}
.datetime{
       font-size: 0.9rem;
       color: black;

}
.bodycontent{
        font-size: 1.4rem;

   
}
/*POST PAGE STYLING END.. deletebtn*/


/*NEWPOST SECTION STYLI */

.uploadpostarea{
  background-color: rgb(197, 197, 73);
  text-align: center;
  height: 50vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.uploadtitle {

  color: blue;
  font-size: 1.8rem;
  text-transform: capitalize;
  outline: 1px solid redd;
  margin: 10px 0;
}
.inputtitle,.inputcontent{
  outline: none;
  border: 2px solid red;
  font-size: 1.2rem;

}

.submitbtn{
  width: 150px;
  height: 50px;
  font-size: 1.3rem;
  color: white;
  border: none;
  border-radius: 10px;
  margin: 10px 0 ;
  background-color: blueviolet;
  cursor: pointer;
}
/*NEWPOST SECTION STYLING END*/



/*ABOUT SECTION STYLING START*/

.aboutarea{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
    background-color: cadetblue;
    height: 50vh;
  text-align: center;

}
.abouttitle{
  font-size: 2.5rem;
            
}
.aboutpara{
  padding: 1rem 2rem 2rem;
  text-transform: capitalize;
  font-size: 1.3rem;

}
/*ABOUT SECTION STYLING END*/


.nopostdisplaymsg{
  display: flex;
  justify-content: center;
  align-items: center;
   height: 50vh;
   font-size: 1.8rem;
}